/* 0. Reset, variables, mixins ================================= */
@import 'reset';
@import 'variables';

/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

body{
	background: white;
	font-family: "Roboto Mono";
}

/* 2. Content ================================= */


a{
	color: $header-color;
	text-align: center;
	display: block;
	text-decoration: none;

	&:hover{
		filter: blur(5px);
	}

	&.selected{
		font-weight: bold;
	}
}

h1{
	font-size: 2em;
	@include bottom-padding;
}

.nav li{
	font-size: 1.2em;
	@include bottom-padding;

	&:last-child{
		margin-bottom: 30px;
	}
}

.hero{
	background: $box-color;
	color: #fff;
	padding: 7em 0;
	margin-bottom: 48px;
}

.col{
	background: $box-color;
	padding: 1em;
	margin: 0 2em 1.8em;
	@include border-radius;
	text-align: center;
}

.special{
	background: $special-color;
	box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75), 0px 0px 18px 3px rgba(0,0,0,0.75);

	h3{
		text-decoration: underline overline;
		font-size: 1.5em;
	}

	h4{
		text-decoration: none;
	}

	.button{
		background-color: yellow;
		color: black;
		border: 1px solid black;
	}
}

h2{
	font-size: 1.5em;
	text-align: center;
}

h3{
	font-size: 1.25em;
	@include bottom-padding;
	font-weight: bold;
	text-decoration: underline;
}

h4{
	@extend h3;
	text-transform: uppercase;
	text-decoration: none;
}

.discount{
	text-decoration: line-through;
	padding-right: 10px;
}

.button{
	background: $button-color;
	padding: .625em 1.25em;
	display: inline-block;
	color: #fff;
	@include border-radius;
	text-align: left;
}

.features li{
	list-style: disc outside none;
	display: list-item;
	margin-left: 1em;
	text-align: left;
	
	&:last-child{
		@include bottom-padding;
	}
}

p:nth-child(6n) {  
	@include bottom-padding;
}


